.i {
    display: flex;
    height: 100vh;
}
.i-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.i-right {
    flex: 1;
    position: relative;
}
.i-left-wrapper {
    padding: 50px;
    height: 50%;
    background-color: burlywood;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.i-intro {
    font-size: 30px;
    font-weight: 300;
}
.i-name {
    font-size: 60px;
}
.i-title {
    height: 50px;
    overflow: hidden;
}
.i-title-wrapper {
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}
@keyframes move {
    25%{
        transform: translateY(-50px);
    }
    50%{
        transform: translateY(-100px);
    }
    75%{
        transform: translateY(-150px);
    }
    100%{
        transform: translateY(-200px);
    }
}
.i-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    /* color: green; */
    background-color: purple;
    display: flex;
    align-items: center;
    padding-left: 20px;
}
.i-bg {
    clip-path: polygon(100% 0%, 100% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color: coral;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}
.i-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: relative;
}

@media screen and (max-width: 480px) {
    .i {
        flex-direction: column;
    }
    .i-left-wrapper {
        padding: 10px 10px 0px 10px;
        margin-bottom: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
    }
    .i-desc {
        display: none;
    }
    .i-bg {
       /*  top: 10px; */
    }
}